<template>
  <div>
    <fieldset>
      <legend>è°è¯ä¿¡æ¯</legend>
      <el-form :model="form" label-width="80px" :rules="rules">
        <el-col :span="18">
          <el-form-item label="å°å" prop="address">
            <el-input v-model="form.address" placeholder="è¯·è¾å¥debugå°å" ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="18">
          <el-form-item label="ç«¯å£" prop="port">
            <el-input v-model="form.port" placeholder="è¯·è¾å¥debugç«¯å£"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="18">
          <el-form-item label="ç®æ ç½ç«" prop="url">
            <el-input v-model="form.url" placeholder="ç®æ ç½ç«å¯è®¿é®çurl(å¨è·¯å¾)"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="18">
          <el-form-item>
            <el-button type="primary" @click="onSubmit">ä¿®æ¹</el-button>
