<script>
import {setArg} from "../utils/settingApi";

export default {
  name: "BugInfo",
  data() {
    let IpReg = /^(([1-9]?\d|1\d{2}|2[0-4]\d|25[0-5])\.){3}([1-9]?\d|1\d{2}|2[0-4]\d|25[0-5])$/,
      urlReg = /^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\*\+,;=.]+$/,
      validateIp = (rule, value, callback) => {
        if (!IpReg.test(value)) {
          callback(new Error('å°åæ ¼å¼å¼å¸¸'))
        } else {
          this.form.IpAllow = true
          callback()
        }
      },
      validatePort = (rule, value, callback) => {
        let number = parseInt(value)
        if (window.isNaN(number)) {
          callback(new Error('ä»æ¯ææ°å­æ ¼å¼'))
        } else if (number <= 0 || number > 65535) {
          callback(new Error('è¶åºç«¯å£èå´'))
        } else {
          this.form.PortAllow = true
          callback()
        }
      },
      validateUrl = (rule, value, callback) => {
        if (!urlReg.test(value)) {
          callback(new Error('urlå°åæ ¼å¼å¼å¸¸'))
        } else {
          this.form.urlAllow = true
          callback()
        }
      }
    return {
      form: {
        address: '',
        port: '',
        url: '',
        IpAllow: false,
        PortAllow: false,
