          {validator: validateIp, trigger: 'blur'}
        ],
        port: [
          {required: true, message: 'ç«¯å£ä¸å¯ä¸ºç©º', trigger: 'blur', allow: false},
          {validator: validatePort, trigger: 'blur'}
        ],
        url: [
          {required: true, message: 'urlæ ¼å¼ä¸æ­£ç¡®', trigger: 'blur', allow: false},
          {validator: validateUrl, trigger: 'blur'}
        ],
      },
    }
  },
  methods:{
    onSubmit() {
      if (this.form.IpAllow && this.form.PortAllow && this.form.urlAllow) {
        let Form = new Object()
        Form.address = this.form.address;
        Form.port = this.form.port;
        Form.url = this.form.url;
        setArg(Form).then(res => {
          if (res.data.code !== 200) {
            this.$message.error(res.data.msg);
          } else {
            this.$message.success(res.data.msg);
            this.$router.push("/main")
          }
        }).catch(err => {
          this.$message.error("åé¨æå¡å¼å¸¸ï¼è¯·æ£æ¥æå¡ç«¯å£æ¯å¦æ­£å¸¸äºå¿");
        })
      } else {
        this.$message.error("åæ°å¼å¸¸ï¼æ£æ¥è¾å¥åæ°")
      }
    }
  }
}
</script>

<style scoped>
